.header {
	@include lato;
	position: absolute;
	display: flex;
	justify-content: space-between;
	width: 100%;
	padding: 30px;
	z-index: 3;
	&__right {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	a {
		text-decoration: none;
		color: #fff; } }
.burger {
	vertical-align: middle;
	background: none;
	width: 24px;
	height: 16px;
	position: relative;
	& > span {
		display: block;
		width: 100%;
		height: 2px;
		background: #fff;
		border-radius: 2px;
		position: absolute;
		top: 7px;
		left: 0;
		// transition: left .25s ease-in-out
		&:nth-child(1),
		&:nth-child(3) {
			transition: transform .25s ease-in-out, left .25s ease-in-out; }
		&:nth-child(1) {
			top: 0; }
		&:nth-child(3) {
			top: 14px; }
		&:nth-child(2) {
			transition: opacity 0.25s linear; } }
	&.is-active {
		& > span {
			&:nth-child(2) {
				opacity: 0; }

			&:nth-child(1) {
				left: -10px;
				transform: rotate(45deg) translateX(10px); }
			&:nth-child(3) {
				left: -10px;
				transform: rotate(-45deg) translateX(10px); } } }

