html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  position: relative;
}

body:before {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
  width: 100%;
  content: "";
  background-color: #000;
  opacity: 0;
  z-index: 1;
  pointer-events: none;
  transition: opacity 0.5s linear;
}

body.sidemenu-is-open {
  overflow: hidden;
}

body.sidemenu-is-open:before {
  opacity: 0.5;
  background-color: #000;
  pointer-events: auto;
}

.btn {
  padding: 25px 35px;
  border: 3px solid #fff;
  text-transform: uppercase;
  transition: background-color 0.25s linear, color 0.25s linear;
}

.btn:hover {
  color: #000;
  background-color: #fff;
}

.header {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 30px;
  z-index: 3;
}

.header__right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header a {
  text-decoration: none;
  color: #fff;
}

.burger {
  vertical-align: middle;
  background: none;
  width: 24px;
  height: 16px;
  position: relative;
}

.burger > span {
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;
  border-radius: 2px;
  position: absolute;
  top: 7px;
  left: 0;
}

.burger > span:nth-child(1), .burger > span:nth-child(3) {
  transition: transform .25s ease-in-out, left .25s ease-in-out;
}

.burger > span:nth-child(1) {
  top: 0;
}

.burger > span:nth-child(3) {
  top: 14px;
}

.burger > span:nth-child(2) {
  transition: opacity 0.25s linear;
}

.burger.is-active > span:nth-child(2) {
  opacity: 0;
}

.burger.is-active > span:nth-child(1) {
  left: -10px;
  transform: rotate(45deg) translateX(10px);
}

.burger.is-active > span:nth-child(3) {
  left: -10px;
  transform: rotate(-45deg) translateX(10px);
}

.login__link {
  text-transform: uppercase;
}

.login li {
  display: inline-block;
  padding-right: 20px;
}

.hero {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  height: 100vh;
  text-align: center;
  color: #fff;
  display: flex;
}

.hero__title {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 72px;
  margin-bottom: 30px;
}

.hero__in {
  margin: 0 auto;
  padding-top: 15%;
}

.hero__start {
  display: inline-block;
}

.hero__action {
  margin-top: 30px;
}

.hero a {
  text-decoration: none;
  color: #fff;
}

.navigation {
  background: linear-gradient(220deg, #d9e6af, #f0af82, #e68c78);
  background-size: 600% 600%;
  animation: navigationGradient 5s ease-in-out infinite;
  transform: translateX(100%);
  top: 0;
  right: 0;
  bottom: 0;
  left: auto;
  position: fixed;
  width: 400px;
  z-index: 2;
}

.nav {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  margin-top: 25%;
}

.nav__item {
  padding-top: 40px;
  text-align: center;
  text-transform: uppercase;
}

.nav a {
  text-decoration: none;
  color: #fff;
}

@keyframes navigationGradient {
  0% {
    background-position: 0% 88%;
  }
  50% {
    background-position: 100% 13%;
  }
  100% {
    background-position: 0% 88%;
  }
}
